import React from 'react';
import { InputGroup, Button, Form } from 'react-bootstrap';

function QuantitySelector({ quantity, onChange, min = 1, max = 100 }) {
  const handleDecrease = () => {
    if (quantity > min) onChange(quantity - 1);
  };

  const handleIncrease = () => {
    if (quantity < max) onChange(quantity + 1);
  };

  const handleChange = (e) => {
    const value = Math.min(Math.max(min, Number(e.target.value)), max);
    onChange(value);
  };

  return (
    <InputGroup size="sm" style={{ width: '120px' }}>
      <Button variant="outline-secondary" onClick={handleDecrease}>-</Button>
      <Form.Control
        type="number"
        value={quantity}
        onChange={handleChange}
        min={min}
        max={max}
        style={{ textAlign: 'center' }}
      />
      <Button variant="outline-secondary" onClick={handleIncrease}>+</Button>
    </InputGroup>
  );
}

export default QuantitySelector;
